@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
:root {
  --accent-color: #59005e;
  --main-bg: #101010;
  --main-text-color: #faf2fb;
  --main-info-color: #b6bfb7;
  --main-success-color: #4CAF50;
  --main-danger-color: #E53935;
  --secondary-bg: #332633;
  --secondary-color: #EEEEEE;
  --selection-bg: #8c0692;
  --selection-color: #ffffff;
  --monochrome: #ffffff;
  --default-transparent: transparent;
  --default-transparent-02: rgb(0 0 0 / 20%);
  --default-transparent-04: rgb(0 0 0 / 40%);
  --default-transparent-06: rgb(0 0 0 / 60%);
  --default-transparent-08: rgb(0 0 0 / 80%);
  --white-transparent-04: rgb(255 255 255 / 40%);
}
* {
  outline: none; 
  box-sizing: border-box;
  text-decoration: none;
  margin: 0;
  padding: 0;
}
::selection {
  color: #ffffff;
  color: var(--selection-color);
  background: #8c0692;
  background: var(--selection-bg);
  text-shadow: none;
}
body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 1.4;
  max-width: 600px;
  margin: 0 auto;
  color: #faf2fb;
  color: var(--main-text-color);
  background: #101010;
  background: var(--main-bg);
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: var(--default-transparent);
  -webkit-app-region: no-drag;
}

.none {
  display: none;
}

.loader {
  display: flex;
}

.space {
  margin: 16px 0;
}

.react-thumbnail-generator {
  display: none;
}

.react-tabs__tab-list {
  border-bottom: 2px solid #332633;
  border-bottom: 2px solid var(--secondary-bg);
  margin: 8px 0 16px;
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  bottom: -2px;
  position: relative;
  list-style: none;
  padding: 8px 16px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.react-tabs__tab:first-child {
  margin-left: 8px;
}

.react-tabs__tab--selected {
  background: #332633;
  background: var(--secondary-bg);
  color: #EEEEEE;
  color: var(--secondary-color);
  border-radius: 6px 6px 0 0;
}

.react-tabs__tab--disabled {
  color: rgb(255 255 255 / 40%);
  color: var(--white-transparent-04);
  cursor: default;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

.layout {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.layout_title {
  margin: 16px 8px 24px;
  position: relative;
  top: -36px;
}

@media (max-width: 400px) {
  .layout_title {
    text-align: center;
  }
}

.text_input {
  display: flex;
  margin: 8px;
  width: calc(100% - 16px);
  height: 36px;
  min-width: 200px;
  max-width: 320px;
  padding: 0 8px;
  background-color: var(--default-transparent);
  color: var(--secondary-color);
  border: 2px solid var(--secondary-bg);
  border-radius: 6px;
  font-size: 15px;
  transition: border-color .2s;
}
.text_input:hover, .text_input:focus {
  border-color: var(--accent-color);
}


.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 12px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  min-width: 100px;
  height: 36px;
  border-radius: 6px;
  margin: 8px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  transition: background-color .1s;
}
.btn:hover::before {
  background-color: var(--default-transparent-02);
}

.btn.main {
  background-color: var(--secondary-bg);
}

.btn.disable {
  pointer-events: none;
  cursor: not-allowed;
}
.btn.disable::before {
  background-color: var(--default-transparent-04);
}

.file_input {
  display: flex;
  align-items: center;
  padding: 0 10px 0 2px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  min-width: 200px;
  max-width: 320px;
  height: 36px;
  border: 2px solid var(--secondary-bg);
  border-radius: 6px;
  margin: 8px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.file_input .btn {
  height: 28px;
  margin: 0 8px 0 0;
  border-radius: 3px;
  font-size: 15px;
}
.file_input .btn::before {
  border-radius: 3px;
}

.file_input span {
  color: var(--secondary-color);
  font-size: 13px;
  max-width: 180px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.radio_group {
  margin: 16px;
  display: flex;
  align-items: center;
}

.radio_item {
  margin-right: 16px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.radio_item label {
  display: flex;
  align-items: center;
  position: relative;
  color: var(--secondary-color);
  padding-left: 28px;
  cursor: pointer;
}
.radio_item label:before, .radio_item label:after {
  content: '';
  position: absolute;
  border-radius: 50px;
  transition: all .3s ease;
  transition-property: transform, border-color;
}
.radio_item label:before {
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  border: 2px solid var(--white-transparent-04);
}
.radio_item label:after {
  top: 5px;
  left: 5px;
  width: 8px;
  height: 8px;
  transform: scale(0);
  background: var(--accent-color);
}
.radio_item input[type="radio"] {
  display: none;
}
.radio_item input[type="radio"]:checked + label {
  color: var(--main-text-color);
}
.radio_item input[type="radio"]:checked + label:before {
  border-color: var(--accent-color);
  animation: ripple .3s linear forwards;
}
.radio_item input[type="radio"]:checked + label:after {
  transform: scale(1);
}
@keyframes ripple {
  0% {
    box-shadow: 0px 0px 0px 1px rgba(255, 255, 255, 0);
  }
  50% {
    box-shadow: 0px 0px 0px 15px rgba(255, 255, 255, .1);
  }
  100% {
    box-shadow: 0px 0px 0px 15px rgba(255, 255, 255, 0);
  }
}

